@import url('https://fonts.googleapis.com/css2?family=Special+Elite&display=swap');
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #eee;
  // font-family: 'Indie Flower', cursive;
  font-family: 'Special Elite', cursive, sans-serif;
}

img {
  width: 190px;
  height: 200px;
}

#root {
  min-height: 100vh;
  min-width: 100vw; 
}

.app {
  min-height: 100vh;
  min-width: 100vw;
}

.header {
  
  background-color: #ddd;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 1rem 0 1rem 0;
}
.title-section {
  font-size: 2.5rem;

}
.score-section {
  display: flex;
  gap: 2rem;
  font-size: 1.5rem;
}

.main {
  flex: 1;
  min-width: 600px;
  padding: 2rem 0 2rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.grid {
  width: 80%;
  display: grid;
  align-items: center;
  justify-content: center;
  grid-template-columns: repeat( auto-fit, 225px);
  margin: 0 auto;
  gap: 1rem;

}

.card {
  width: 200px;
  height: 250px;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  background-color: #ddd;

  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;

  &:hover {
    cursor: pointer;
    transform: scale(1.05);
  }
}

.card-text {
  width: 100%;
  text-align: center;
  font-size: 1.5rem;
}